import { IPosition, ISceneObject, IUpdatable, IAIControlled, IHittable } from "./IScene";

export abstract class SceneObject implements ISceneObject, IAIControlled, IHittable {

    public readonly element: HTMLElement = this._create();
    public position: IPosition = { x: 0, y: 0 };
    
    protected _ai: IUpdatable[] = [];

    public update(diff: number): void {
        for (const ai of this._ai) {
            ai.update(diff);
        }
    }

    public render(): void {
        this.element.style.left = this.position.x + "px";
        this.element.style.top = this.position.y + "px";
    }  

    public addAI(ai: IUpdatable) {
        this._ai.push(ai);
    }

    public hit(damage: number): void {
        this.element.dispatchEvent(new CustomEvent('hit', { detail: damage }));
    }  
    
    protected abstract _create(): HTMLElement;
}